background: $table_bg_main;\n      color: $table_text;\n    }\n\n    &--opened {\n      height: 56px;\n      font-size: 15px;\n      background: $table_bg_main;\n      color: $table_text;\n      border-bottom: $btn_border;\n    }\n\n    //&--opened.table__data--current {\n    //  border-bottom: 2px solid black;\n    //}\n\n    &--current {\n      font-weight: 600;\n      color: $table_text;\n      border: 2px solid black;\n      //font-size: 16px;\n    }\n  }\n\n  &__db {\n    display: flex;\n    min-width: 70px;\n    max-width: 70px;\n  }\n\n  &__timestamp {\n    min-width: 110px;\n    max-width: 110px;\n  }\n\n  &__files {\n    min-width: 35px;\n    max-width: 35px;\n    position: relative;\n  }\n\n  &__number {\n    min-width: 120px;\n    max-width: 120px;\n  }\n\n  &__sample {\n    min-width: 60px;\n    max-width: 60px;\n  }\n\n  &__client {\n    min-width: 160px;\n    max-width: 160px;\n  }\n\n  &__name {\n    min-width: 260px;\n    max-width: 260px;\n  }\n\n  &__material {\n    min-width: 140px;\n    max-width: 140px;\n  }\n\n  &__quantity {\n    min-width: 70px;\n    max-width: 70px;\n  }\n\n  &__issued {\n    min-width: 70px;\n    max-width: 70px;\n    border-right: $btn_border !important;\n\n    &--done {\n      color: green;\n      font-size: 16px;\n      font-weight: 600;\n      //animation: issued-ready infinite 4s;\n    }\n  }\n\n  &__complete {\n    border-right: none !important;\n    border-top: $btn_border;\n    border-bottom: $btn_border;\n\n    input {\n      height: 26px;\n    }\n  }\n\n  &__m {\n    min-width: 36px;\n    max-width: 36px;\n  }\n\n  &__endtime {\n    min-width: 140px;\n    max-width: 140px;\n\n    &--dead {\n      color: red;\n      font-size: 15px;\n    }\n  }\n\n  &__route {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    height: 29px;\n    width: 50px;\n\n    &:last-child {\n      border-right: $btn_border !important;\n    }\n  }\n\n  &__routes {\n    min-width: 500px;\n    max-width: 500px;\n  }\n\n  &-routes__issued {\n    //border-top: 1px solid black;\n  }\n\n  &__p {\n    min-width: 60px;\n    max-width: 60px;\n  }\n\n  &-p-select,\n  &-m-select {\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    appearance: none;\n    cursor: pointer;\n  }\n\n  &__comment {\n    flex-grow: 1;\n    min-width: 200px;\n    max-width: 100%;\n\n    &--top {\n      flex-grow: 1;\n      min-width: 200px;\n      max-width: 100%;\n    }\n  }\n\n  &-routes {\n    &__wrapper {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      height: 28px;\n    }\n  }\n}\n\n.files__ico {\n  width: 30px;\n  height: 20px;\n}\n\n.table-body {\n  //&__trattr {\n  //  position: relative;\n  //\n  //  &:hover:after {\n  //    box-sizing: border-box;\n  //    min-width: 260%;\n  //    width: auto;\n  //    text-align: center;\n  //    content: attr(data-title);\n  //    position: absolute; /* Абсолютное позиционирование */\n  //    left: 0;\n  //    top: 130%; /* Положение подсказки */\n  //    z-index: 1; /* Отображаем подсказку поверх других элементов */\n  //    background: rgba(255, 255, 230, 0.9); /* Полупрозрачный цвет фона */\n  //    font-family: Arial, sans-serif; /* Гарнитура шрифта */\n  //    font-size: 11px; /* Размер текста подсказки */\n  //    padding: 5px 5px; /* Поля */\n  //    border: 1px solid #333;\n  //  }\n  //}\n  //\n  //&__attr {\n  //  position: relative;\n  //\n  //  &:hover:after {\n  //    box-sizing: border-box;\n  //    min-width: 100%;\n  //    text-align: center;\n  //    content: attr(data-title);\n  //    position: absolute; /* Абсолютное позиционирование */\n  //    left: 0;\n  //    top: 130%; /* Положение подсказки */\n  //    z-index: 1; /* Отображаем подсказку поверх других элементов */\n  //    background: rgba(255, 255, 230, 0.9); /* Полупрозрачный цвет фона */\n  //    font-family: Arial, sans-serif; /* Гарнитура шрифта */\n  //    font-size: 11px; /* Размер текста подсказки */\n  //    padding: 5px 5px; /* Поля */\n  //    border: 1px solid #333;\n  //  }\n  //}\n\n\n  &_cell {\n    max-height: 56px;\n    font-size: 16px;\n    border-right: $btn_border;\n    border-bottom: $btn_border;\n    position: relative;\n\n    &:last-child {\n      border-right: none;\n    }\n\n    &--opened {\n      height: 56px;\n    }\n  }\n}\n\n@keyframes issued-ready {\n  0% {\n    background: white;\n    color: black;\n  }\n\n  50% {\n    background: green;\n    color: white;\n  }\n\n  100% {\n    background: white;\n    color: black;\n  }\n}\n\n.table__route {\n  &:first-child {\n    border-left: none;\n  }\n\n  &--date {\n    flex-grow: 1;\n    //overflow: auto;\n    //width: 100%;\n  }\n\n  &--issued {\n    border-top: none;\n    max-height: 28px;\n\n    &:last-child {\n      border-right: $btn_border;\n    }\n\n    &:first-child {\n      border-left: none;\n    }\n\n    input {\n      height: 26px;\n    }\n  }\n}\n\n.route {\n  color: rgb(107, 102, 102);;\n  height: 24px;\n  width: 48px;\n  border-radius: 15px;\n  border: 2px solid transparent;\n\n  &--inplan {\n    //border-top: 2px solid $text_gray;\n    //border-bottom: 2px solid $text_gray;\n    //border-right: 2px solid $text_gray;\n    //border-left: 2px solid $text_gray !important;\n    //background: rgb(197 195 195);\n    background: rgb(181 173 173);\n    //background: rgb(213 220 213);\n\n    .route {\n      background: white;\n      border-radius: 15px;\n    }\n  }\n\n  &--planned {\n    //text-decoration: underline;\n    //font-style: italic;\n  }\n\n  &--paused {\n    border: 2px solid #51a5e3;\n    background: white;\n    //color: white !important;\n    //border-radius: 15px;\n  }\n\n  &--error {\n    color: white !important;\n    background: rgba(210, 66, 66, 1) !important;\n    //border-radius: 15px;\n  }\n\n  &--started {\n    background: white !important;\n    border: 2px solid #e06c00;\n    //border-radius: 15px;\n  }\n\n  &--completed {\n    background: white !important;\n    border: 2px solid rgba(0, 130, 29, 1);\n    //border-radius: 15px;\n  }\n}\n\n.table-info {\n  display: flex;\n  align-items: center;\n}\n\n.order__copy {\n  width: 10px;\n  border: none;\n  color: $text_black;\n  background: rgb(156, 156, 156) !important;\n  transition: color .3s;\n\n  &:hover {\n    cursor: pointer;\n    transition: color .3s;\n    color: white;\n  }\n}\n\n.order__delete {\n  width: 20px;\n  border: none;\n  background: red;\n  transition: color .3s;\n\n  &:hover {\n    cursor: pointer;\n    transition: color .3s;\n    color: white;\n  }\n}\n\n.table {\n  &__issued--report {\n    min-width: 105px;\n    max-width: 105px;\n  }\n\n  &__plan--report {\n    min-width: 105px;\n    max-width: 105px;\n  }\n\n  &__route--report {\n    min-width: 75px;\n    max-width: 75px;\n  }\n\n  &__route--date {\n    //border-right: 1px solid black;\n    //min-width: 750px;\n\n    &__list {\n      display: flex;\n      align-items: center;\n    }\n\n    &__item {\n      margin-right: 5px;\n      height: 28px\n    }\n  }\n\n  &__operator--report {\n    min-width: 95px;\n    max-width: 95px;\n  }\n\n  &__issued-plan--report {\n    min-width: 120px;\n    max-width: 120px;\n  }\n}","@import \"var\";\n\n* {\n  padding: 0;\n  margin: 0;\n  box-sizing: border-box;\n  //font-family: 'Yanone Kaffeesatz', sans-serif;\n  font-family: 'Nunito', sans-serif;\n}\n\ninput::-webkit-outer-spin-button,\ninput::-webkit-inner-spin-button {\n  display: none;\n  -webkit-appearance: none;\n  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */\n}\n\ninput[type=number] {\n  -moz-appearance: textfield;\n}\n\ninput::-webkit-outer-spin-button,\ninput::-webkit-inner-spin-button {\n  /* display: none; <- Crashes Chrome on hover */\n  -webkit-appearance: none;\n  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */\n}\n\ninput, select {\n  outline: none;\n  //font-family: 'Yanone Kaffeesatz', sans-serif;\n  font-family: 'Nunito', sans-serif;\n}\n\nhtml {\n  height: 100%;\n  